.sr-only {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  overflow: visible;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0;
}

/* Toastr - Styles */
.toast-title {
  font-weight: bold;
}

.toast-message {
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

.toast-message a,
.toast-message label {
  color: #fff;
}

.toast-message a:hover {
  text-decoration: none;
  color: #ccc;
}

.toast-close-button {
  font-size: 20px;
  font-weight: bold;
  position: relative;
  top: -.3em;
  right: -.3em;
  float: right;
  opacity: .8;
  color: #fff;
  text-shadow: 0 1px 0 #fff;
}

.toast-close-button:hover,
.toast-close-button:focus {
  cursor: pointer;
  text-decoration: none;
  opacity: .4;
  color: #000;
}

/*Additional properties for button version
     iOS requires the button element instead of an anchor tag.
     If you want the anchor version, it requires `href="#"`.*/
button.toast-close-button {
  padding: 0;
  cursor: pointer;
  border: 0;
  background: transparent;
  -webkit-appearance: none;
}

.toast-top-center {
  top: 0;
  right: 0;
  width: 100%;
}

.toast-bottom-center {
  right: 0;
  bottom: 0;
  width: 100%;
}

.toast-top-full-width {
  top: 0;
  right: 0;
  width: 100%;
}

.toast-bottom-full-width {
  right: 0;
  bottom: 0;
  width: 100%;
}

.toast-top-left {
  top: 12px;
  left: 12px;
}

.toast-top-right {
  top: 12px;
  right: 12px;
}

.toast-bottom-right {
  right: 12px;
  bottom: 12px;
}

.toast-bottom-left {
  bottom: 12px;
  left: 12px;
}

#toast-container {
  position: fixed;
  z-index: 999999;
  pointer-events: none;
    /*overrides*/
}

#toast-container * {
  box-sizing: border-box;
}

#toast-container > div {
  font-size: 14px;
  line-height: 1.3;
  position: relative;
  overflow: hidden;
  width: 400px;
  margin: 0 0 6px;
  padding: 10px 10px 10px 50px;
  pointer-events: auto;
  color: #fff;
