  padding: 0 15px 8px;
  transition: background .15s ease-in-out;
  border: 1px solid transparent;
  background: #f5f5f5;
}

.col-arcoffice:not(.is-disabled):not(.is-loading):not(.col-visite):hover,
.col-arcoffice.is-active {
  background: #dfddea;
}

.col-arcoffice:not(.is-disabled):not(.is-loading):not(.col-visite):hover .form-title:before,
.col-arcoffice.is-active .form-title:before {
  transform: scaleX(80);
  transform-origin: left;
  opacity: 1;
}

.col-arcoffice.is-disabled {
  user-select: none;
}

.col-arcoffice.is-disabled:before {
  position: absolute;
  z-index: 998;
  top: 40px;
  right: 0;
  bottom: 0;
  left: 0;
  content: '';
  background: rgba(233, 236, 239, .6);
}

