  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  margin-top: -25px;
  margin-left: -25px;
  content: '';
  animation: rotate .6s infinite linear;
  border: 4px solid #1471d4;
  border-right-color: rgba(20, 113, 212, .4);
  border-radius: 50%;
}

