  padding: 0;
}

.form-title {
  font: 600 1rem 'Montserrat', Helvetica, Arial, sans-serif;
  position: relative;
  height: 40px;
  margin: 0 -16px 8px -16px;
  padding: 8px 10px;
  letter-spacing: -.5px;
  color: #3e396b;
  border-bottom: 1px solid rgba(0, 0, 0, .15);
}

.form-title:before {
  position: absolute;
  bottom: -1px;
  width: 1px;
  height: 2px;
  content: '';
  transition: transform .15s ease-in-out;
  opacity: 0;
  background: #1471d4;
}

.form-title .btn {
  font-size: 12px;
  line-height: 1.1;
  min-width: 24px;
  padding: .25rem .3rem;
}

.form-lead {
  font-size: .875rem;
  font-weight: 600;
  color: #1471d4;
}

.arcoffice-fosuser {
  position: relative;
}

.arcoffice-fosuser:before {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  content: '';
  background-image: url(../img/bg-01.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.arcoffice-fosuser main {
  margin-top: 0;
}

.arcoffice-fosuser main > div {
  height: calc(100vh - 50px);
}

.arcoffice-fosuser .lead {
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  font-weight: 600;
  letter-spacing: -.5px;
  color: #3e396b;
}

.arcoffice-fosuser .arcoffice-fosuser-sub {
  width: 100%;
  max-width: 375px;
  margin: 0 15px;
  padding: 25px;
  border-radius: .25rem;
  background: rgba(255, 255, 255, .5);
  box-shadow: 0 5px 20px #cdcbde;
}

.arcoffice-fosuser .arcoffice-fosuser-sub label:not(.form-check-label) {
  font-size: 1rem;
}

.arcoffice-fosuser .arcoffice-fosuser-sub input[type='submit'] {
  font: 600 1rem 'Montserrat', Helvetica, Arial, sans-serif;
}

footer {
  height: 50px;
}

footer .container {
  padding: .875rem 0;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%);
    transform: rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%);
    transform: rotate(360deg);
  }
}

.b-lazy {
  max-width: 100%;
  transition: opacity .2s ease;
  opacity: 0;
}

.b-lazy.b-loaded {
  opacity: 1;
}

[v-cloak] {
  display: none;
}

.tt-menu {
  font-size: 13px;
  z-index: 10000 !important;
  top: 99%;
  width: calc(100% - .5rem);
  border: 1px solid #80bdff;
  border-radius: .25rem;
  background-color: #fff;
}

.tt-suggestion {
  padding: 6px 5px;
}

.tt-suggestion:hover {
  cursor: pointer;
  color: #fff;
  background: #1471d4;
}

.tt-cursor {
  background: rgba(20, 113, 212, .2);
}

#cometchat {
  position: relative;
}

.dz-preview {
  display: none;
}

.invalid-feedback,
.invalid-tooltip {
  display: inherit;
}
